.header {
  display: flex;
  /* flex-direction: row; */
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
}

.header-logo {
  height: 25px;
  object-fit: contain;
  margin-left: 20px;
}

.header-input {
  display: flex;
  align-items: center;
  width: 40%;
  border: 1px solid lightgray;
}
.header-icon {  
  margin-right: 5px;
}

.header-input-btn {
  width: 50px !important;
  background-color: #fafafa;
  border-left: 1px solid lightgray;
  color: gray;
}

.header-icons {
  display: flex;
  align-items: center;
}

.header-input > input {
  flex: 1;
  border: none;
}

@media (max-width: 450px) {

  .header-icons { display: none;}
}
  